*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    overflow: hidden;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.loading-bar{
    position:absolute;
    top:50%;
    width:100%;
    height:2px;
    background:#ffffff;
    transform: scaleX(0);
    transform-origin: top left;
    transition: transform 0.5s;
    will-change: transform;

}

.loading-bar.ended{

    transform-origin: top right;
    transition: transform 1s ease-in-out;
}

.point {
    position: absolute;
    top:50%;
    left:50%;
}

.point:hover .text{
    opacity: 1;
    
}



.point .label{
    position:absolute;
    top:-20px;
    left:-20px;
    border-radius: 50%;
    width:40px;
    height:40px;
    background:#00000077;
    color: #ffffff;
    font-family: Helvetica,Arial,sans-serif;
    text-align: center;
    line-height:40px;
    font-weight:100;
    font-size: 14px;
    cursor: help;
    transform: scale(0,0);
    transition: transform 0.3s;

}

.point.visible .label{
    transform: scale(1,1);
    background: green;
}


.point .text 
{
    position:absolute;
    top: 30px;
    left:-120px;
    width: 200px;
    padding:20px;
    border-radius: 4px;
    background: #33EC3640;
    line-height: 1.3em;
    font-family: Arial, Helvetica, sans-serif;
    color:white;
    font-weight:100;
    font-size:14px;
    opacity: 0;
    transition: opacity 0.3s;
    pointer-events: none;
   

}

